* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

img {
  width: 18em;
  height: 28em;
}

.search {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1em;
}

.checkbox-container {
  margin-top: 0.5em;
}

.checkbox-container > * {
  padding: 0.5em;
}

.button-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20%;
  margin-top: 2em;
}

.popup-container {
  display: flex;
  flex-direction: row;
}

.popup-container > div:last-child {
  margin-left: 1.5em;
}
